.link {
  display: flex;
  align-items: center;
  justify-content: center;
}
.linkedin {
  margin-right: 18px;
}

@media screen and (min-width: 768px) {
  .wrap {
    width: 740px;
    padding-left: 32px;
    padding-right: 32px;
  }
  .list {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin-left: -30px;
  }

  .title {
    font-size: 16px;
    line-height: 1.14;
  }

  .wrapImg {
    width: 100%;
  }

  .item {
    width: calc(100% / 2 - 30px);
    margin-left: 30px;
  }

  .img {
    width: 100%;
  }

  .info {
    padding-right: 28px;
  }
  .link {
    left: 235px;
  }
}

@media screen and (min-width: 1280px) {
  .wrap {
    width: 890px;
  }
  .item {
    width: calc(100% / 3 - 30px);
  }
  .link {
    left: 170px;
  }
}
